import { Link } from "gatsby"
import PropTypes from "prop-types"
import React, { useContext, useState } from "react"
import AppContext from '../../contextAPI/AppContext'
import { navigate } from "@reach/router";

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Layers from '@material-ui/icons/Layers';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import Search from '@material-ui/icons/Search';
import SearchPanel from '../SearchPanel/SearchPanel'
import Menu from '../Menu/Menu'
import Drawer from '@material-ui/core/Drawer';
import SegmentTitles from '../../data/SegmentTitles.json'
import Typography from '@material-ui/core/Typography';
import Popover from '@material-ui/core/Popover';
import LayerSelect from '../LayerSelect/LayerSelect'

const useStyles = makeStyles({
  toolBar: {
    justifyContent: 'stretch',
    // '& div': {
    //   justifyContent: 'space-evenly',
    //   border: 'red solid 2px',
    // }
  },
  icons: {
    margin: '3px'
  },
  title: {
    color: `white`,
    textDecoration: `none`,
    flex: '10'
  },
  buttonGroups: {
    flex: '1 1 100px',
    display: 'flex',
  },
  startButtons: {
    display: 'flex',
  },
  endButtons: {
    justifyContent: 'flex-end',
  },
  title: {
    flexGrow: 1,
    textAlign: 'center'
  },
  // title: {
  //   justifyContent: 'center',
  //   border: '1px red solid',
  //   fontSize: '120%',
  //   paddingTop: '10px'
  // },
})

const Header = ({ showIcons, currentLocation, segmentID }) => {
  const classes = useStyles();
  // This controls the visibility of chapter and verse references
  const receivedAppContext = useContext(AppContext);
  const [ menu, updateMenu ] = useState(false)
  const [ searchMenu, updateSearchMenu ] = useState(false)
  const interlinearOption = (receivedAppContext.version === 'GNA' || receivedAppContext.version === 'WLC') ? true : false
  
  //PopOver element toggling open and closed
  const [anchorEl, setAnchorEl] = React.useState(null);

  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleClose() {
    setAnchorEl(null);
  }

  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;

  return (
    <AppBar position="fixed">
    <Toolbar className={classes.toolBar}>
          <div className={`${classes.buttonGroups} ${classes.startButtons}`}>
            <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => updateMenu(!menu)}>
              <MenuIcon />
            </IconButton>
          </div>
          <div className={`${classes.buttonGroups}`}>
          <Typography variant="subtitle2" className={classes.title}>
          {(!showIcons) ? 'SourceView Reader' : (segmentID.substring(0,1) !== 'I') ? `${SegmentTitles[segmentID].book} ${SegmentTitles[segmentID].ref}` : `${SegmentTitles[segmentID].title.split('to ')[1]}`}
          </Typography>
            {/* <p>{(!showIcons) ? 'SourceView Reader' : (segmentID.substring(0,1) !== 'I') ? `${SegmentTitles[segmentID].book} ${SegmentTitles[segmentID].ref}` : `${SegmentTitles[segmentID].title}`}</p> */}
            {/* <Link
              to="/"
              style={{
                color: `white`,
                textDecoration: `none`,
                flex: '10'
              }}
            >
              {siteTitle}
            </Link> */}
          </div>
          <div className={`${classes.buttonGroups} ${classes.endButtons}`}>
            <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={handleClick}>
              <Layers />
            </IconButton>
            <Popover
              id={id}
              open={open}
              anchorEl={anchorEl}
              onClose={handleClose}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'center',
              }}
              transformOrigin={{
                vertical: 'top',
                horizontal: 'center',
              }}
            >
              <LayerSelect />
            </Popover>
            {(showIcons) ? (<IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => receivedAppContext.updateDisplayRef(!receivedAppContext.displayRef)}>
              {(receivedAppContext.displayRef) ? (<Visibility />) : 
              (<VisibilityOff />)}
            </IconButton>) : null}
            <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => updateSearchMenu(!searchMenu)}>
              <Search />
            </IconButton>
          </div>
</Toolbar>
<Drawer 
  className={classes.drawer}
  open={menu} 
  anchor="left"
  width="75%"
  maxWidth="75%"
  overflowX="auto"
  onClose={() => updateMenu(false)}>
    <Menu location={currentLocation} closeMenu={() => updateMenu(false)} />
</Drawer>
<Drawer 
  className={classes.drawer}
  open={searchMenu} 
  anchor="right"
  width="75%"
  onClose={() => updateSearchMenu(false)}>
    <SearchPanel 
      currentLocation={currentLocation.pathname}
      closeSearchMenu={() => updateSearchMenu(false)}/>
</Drawer>
</AppBar>
)}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
